@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap");
.bb--yellow {
  border-bottom: 2px solid #ffdd00;
}

.topmenu {
  background-color: #1a1a1a;
}
.topmenu .topmenu__container {
  display: flex;
  width: 80%;
}
.topmenu .topmenu__container .topmenu__item {
  display: flex;
  display: flex;
  justify-content: flex-end;
  list-style: none;
  margin: 0px;
  padding: 0px;
  width: 100%;
}
.topmenu .topmenu__container .topmenu__item .topmenu__itemlink {
  display: flex;
  justify-content: center;
  margin: 0px 10px;
  font-size: 0.8em;
  height: 25px;
  align-items: center;
}
.topmenu .topmenu__container .topmenu__item .topmenu__itemlink a {
  color: #a9a9a9;
  text-decoration: none;
}

.primarymenu {
  background-color: #101000;
}
.primarymenu .primarymenu__container {
  display: flex;
  width: 80%;
}
.primarymenu .primarymenu__container .primarymenu__logo {
  width: 40%;
  margin-left: 20%;
}
.primarymenu .primarymenu__container .primarymenu__logo .primarymenu__logoimage {
  width: 60%;
}
.primarymenu .primarymenu__container .primarymenu__item {
  display: flex;
  width: 60%;
  display: flex;
  justify-content: flex-end;
  list-style: none;
  margin: 0px;
  padding: 0px;
}
.primarymenu .primarymenu__container .primarymenu__item .primarymenu__itemlink {
  display: flex;
  justify-content: center;
  margin: 0px 10px;
  font-size: 1em;
  text-align: center;
  align-items: center;
}
.primarymenu .primarymenu__container .primarymenu__item .primarymenu__itemlink a {
  color: #ffffff;
  text-decoration: none;
}

.banner {
  position: relative;
}
.banner .banner__container {
  display: flex;
}
.banner .banner__container .banner__image {
  position: absolute;
}
.banner .banner__container .banner__image .banner__imageitem {
  z-index: 0;
}
.banner .banner__container .banner__item {
  display: flex;
  align-items: center;
  position: absolute;
  height: 30px;
  list-style: none;
  margin-top: 8%;
  left: 22%;
  right: 25%;
}
.banner .banner__container .banner__item .banner__form {
  background-color: #ffffff;
  color: #a9a9a9;
  border-radius: 7px;
  border: 2px solid #ffdd00;
  width: 85%;
  height: 25px;
}
.banner .banner__container .banner__item .banner__itemlink {
  background-color: #ffdd00;
  color: #000000;
  margin: 0px 10px;
  width: 15%;
  border: 2px solid #ffdd00;
  border-radius: 5px;
  height: 30px;
}

body {
  margin: 0px;
  font-family: "Lato", sans-serif;
}